@import 'modules/functions';
@import 'modules/color';


@import 'partials/typography';

* { @include clean; box-sizing: border-box; }
//::selection { color: color(black); }

:root {
	@include font(roboto);
	box-sizing: border-box;
	-moz-osx-font-smoothing: grayscale;
	-o-font-smoothing: antialiased;
	-webkit-font-smoothing: antialiased;
	font-smoothing: antialiased;
	line-height: 1.5;
	-ms-overflow-style: -ms-autohiding-scrollbar; // IE11+
	overflow-y: scroll; // All browsers without overlaying scrollbars
	text-rendering: optimizeLegibility;
	text-size-adjust: 100%; // iOS 8+
}

body {
	background-color: color(white);
	color: color(black);
}

header {
	background-color: #1d1f21;
	height: 65px;

	h1 {
		@include font(roboto, regular, 2.25);
		color: color(white);
		line-height: 65px;
		margin: 0;
		text-align: center;
	}
}

main {
	display: flex;
	flex-wrap: wrap;
	margin: 50px auto 0;
	max-width: 1180px;
	width: 90%;

	figure {
		@include clean;
		@include drop-shadow(2);
		margin: 0 20px 40px;
		padding: 5px;
		width: calc(20% - 40px);

		a { color: color(deep-purple); }
		img { max-width: 100%; }
		strong { @include font(roboto, medium, 1.5); }
		svg { height: 24px; padding-left: 5px; position: relative; top: 4px; width: 24px;}
		p { padding: 0; }

		.daemon {
			clip-rule: evenodd;
			fill-rule: evenodd;
			stroke-linejoin: round;
			stroke-miterlimit: 1.41421;

			path {
				fill: #f64636;
				fill-rule: nonzero;
			}
		}

		&:first-child, &:nth-child(5n+1) { margin-left: 0; }
		&:last-child, &:nth-child(5n) { margin-right: 0; }
	}
}
